/*
   Explanation:
   1. `@forward` is part of the Sass module system. It allows re-exporting styles from other Sass files.
   2. This approach promotes modularity by grouping related styles into separate files.
   3. Files importing this module don't need to import each individual file separatelyâ€”they inherit all forwarded content.

   Example usage:
   In a main file (e.g., `main.scss`), you can simply do:
   @use 'settings';
   All variables, functions, and mixins forwarded by 'settings' will be accessible.
*/
/*
   Remove default top margin for headings to maintain consistent spacing
   and prevent unnecessary extra space at the top of sections.
*/
h1, h2, h3 {
  margin-top: 0; /* Eliminates default top margin applied by browsers */
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

/*
   Remove default text decoration (underline) for anchor tags in all states.
   This provides a cleaner, minimal design for links.
*/
a, a:visited, a:active {
  text-decoration: none; /* No underline for links in normal, visited, or active states */
}

/*
   Explanation:
   1. Removing `margin-top` on headings avoids unexpected spacing issues when stacking multiple sections.
   2. Resetting `text-decoration` for links allows better control over link appearance, enabling the use of custom styles (e.g., color, hover effects).
   3. Consider adding hover styles (e.g., `a:hover { text-decoration: underline; }`) for better user experience.
*/
:root {
  --font-inter: "Inter", sans-serif;
}

/*
   Define color variables for primary, social media, and theme-specific colors.
   Using CSS variables makes it easy to switch between dark and light themes.
*/
:root {
  /* Primary Colors */
  --limegreen: hsl(163, 72%, 41%); /* Used for positive UI feedback */
  --brightred: hsl(356, 69%, 56%); /* Used for negative UI feedback */
  /* Social Media Colors */
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 92%, 70%); /* Instagram gradient start */
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%); /* Instagram gradient end */
  --youtube: hsl(348, 97%, 39%);
  /* Toggle Button Colors */
  --toggle-bg-start: hsl(210, 78%, 56%); /* Dark theme toggle gradient start */
  --toggle-bg-end: hsl(146, 68%, 55%); /* Dark theme toggle gradient end */
  --toggle: hsl(230, 22%, 74%); /* Light theme toggle color */
  /* Dark Theme Colors */
  --dark-bg: hsl(230, 17%, 14%); /* Background color for dark theme */
  --dark-top-bg: hsl(232, 19%, 15%); /* Top section background */
  --dark-card: hsl(228, 28%, 20%); /* Card background */
  --dark-text1: hsl(228, 34%, 66%); /* Secondary text color */
  --dark-text2: hsl(0, 0%, 100%); /* Primary text color (white) */
  /* Light Theme Colors */
  --light-bg: hsl(0, 0%, 100%); /* Background color for light theme */
  --light-top-bg: hsl(225, 100%, 98%); /* Top section background */
  --light-card: hsl(227, 47%, 96%); /* Card background */
  --light-text: hsl(228, 12%, 44%); /* Secondary text color */
  --light-text2: hsl(230, 17%, 14%); /* Primary text color (dark blue) */
}

/*
   Notes:
   1. Using HSL (Hue, Saturation, Lightness) makes it easier to tweak colors consistently.
   2. Instagram and dark theme toggle buttons use gradients, defined by start and end colors.
   3. The dark and light themes are defined with separate background, card, and text colors.
   4. These variables can be easily applied using the `var()` function in CSS.

   Example Usage:
   background: var(--dark-bg);
   color: var(--light-text);
*/
.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

/*
   Define styles for the `.toggle` component.
   Use a nested selector for the toggle button to maintain a clear hierarchy.
*/
.toggle {
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 0.3rem 0.5rem;
  border: none;
  margin: 0;
  /*
     Explanation:
     1. Using `&__button` ensures a clear relationship between the parent `.toggle` and its child element, following the BEM (Block Element Modifier) methodology.
     2. The `rem` function converts fixed pixel values to rem units, ensuring responsiveness and accessibility.
     3. `border-radius: 50%` creates a circular button.
     4. `background-color` is set using the `--toggle` variable, which makes it easy to switch themes or update the color globally.
  */
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 0.125rem solid white;
}
.toggle__background {
  border-radius: 0.75rem;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  display: block;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.82%);
  pointer-events: none;
}
.toggle__button { /* BEM naming convention: child element of `.toggle` */
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem; /* Width set using custom rem function for scalability */
  height: 1.125rem; /* Height set using custom rem function for consistency */
  border-radius: 50%; /* Makes the button perfectly circular */
  background-color: var(--dark-bg); /* Background color set using a CSS variable */
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

/*
   Apply consistent padding to the `.card` component using a custom rem function.
   This ensures responsive and scalable spacing based on the root font size.
*/
.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--dark-card);
  color: var(--dark-text1);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: " ";
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
  position: absolute;
  display: block;
  left: 0;
  top: 0;
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--light-bg);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: 400;
  color: var(--dark-text1);
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}
/*
   Explanation:
   1. The `rem` function likely converts pixel values to rems by dividing the given pixel value by the root font size.
   2. Using rem units ensures that spacing scales appropriately if the root font size changes.
   3. This approach enhances accessibility and responsiveness, especially for users who adjust their browser's default font size.

   Example:
   If the root font size is 16px, `rem(20)` returns `1.25rem` (20px / 16px).
*/